@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
.title
{
    color: #38C98D;
    font-weight: 900px;
    font-size: 25px;
    text-align: center;
}
.form-category
{
    margin: 50px;
    text-align: center;
}

.input-category
{
    color: grey;
    border-radius: 20px;
    border: 1px solid #38C98D;
    font-weight: bold;
    font-size: 15px;
    padding: 40px;
    text-align: center;
}

.btn-div
{
    position: relative;
    text-align: center;
    top: 20px; 
}

.btn-primary 
{
    border-radius: 5px;
    border: 1px solid grey;
    background-color: #38C98D;
    font-weight: bold;
    color: #fff;
    margin-left: 10px;
    padding: 8px 20px; 
}

.btn-primary:hover
{
    background-color:  #37b983;
    transition: 1.5s;
}

.btn-secondary
{
    border-radius: 5px;
    border: 1px solid grey;
    background-color: gray;
    font-weight: bold;
    color: #fff;
    margin-left: 10px;
    padding: 8px 20px; 
}
.div-list
{
    margin-bottom:  20px;
    text-align: center;
}

.list
{
    color: #5e5e5e;
    font-weight: bold;
}
*
{
    font-family: 'Roboto', sans-serif;
}
